"use client";

import Link from "next/link";
import {
  Search,
  User,
  ShoppingBag,
  Menu,
  LogOut,
  Package,
  MapPin,
  Settings,
} from "lucide-react";
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Logo } from "@/components/icons";
import { useCart } from "@/lib/cart-context";
import {
  Sheet,
  SheetContent,
  SheetTrigger,
  SheetHeader,
  SheetTitle,
} from "@/components/ui/sheet";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { CartSheetContent } from "@/components/cart/cart-sheet";
import { Input } from "../ui/input";
import { useSession, signOut } from "next-auth/react";
import { LoginDialog } from "@/components/auth/login-dialog";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

const navLinks = [
  { href: "/products", label: "All Products" },
  { href: "/categories/bears", label: "Bears" },
  { href: "/categories/bunnies", label: "Bunnies" },
  { href: "/categories/magical", label: "Magical" },
];

export function Header() {
  const { itemCount } = useCart();
  const [showSearch, setShowSearch] = useState(false);
  const [loginOpen, setLoginOpen] = useState(false);

  const { data: session, status } = useSession();
  const user = session?.user;
  const isUserLoading = status === "loading";

  const getInitials = (name: string | null | undefined) => {
    if (!name) return 'U';
    return name.split(' ').map(n => n[0]).join('');
  }

  return (
    <>
      <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container flex h-16 items-center">
          <div className="mr-4 hidden md:flex">
            <Link href="/" className="mr-6">
              <Logo />
            </Link>
            <nav className="flex items-center space-x-6 text-sm font-medium">
              {navLinks.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className="transition-colors hover:text-foreground/80 text-foreground/60"
                >
                  {link.label}
                </Link>
              ))}
            </nav>
          </div>

          {/* Mobile Nav */}
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon" className="md:hidden">
                <Menu />
                <span className="sr-only">Toggle Menu</span>
              </Button>
            </SheetTrigger>
            <SheetContent side="left">
              <SheetHeader>
                <SheetTitle>
                  <Link href="/" className="mr-6">
                    <Logo />
                  </Link>
                </SheetTitle>
              </SheetHeader>
              <nav className="flex flex-col space-y-4 mt-8">
                {navLinks.map((link) => (
                  <Link
                    key={link.href}
                    href={link.href}
                    className="text-lg transition-colors hover:text-foreground"
                  >
                    {link.label}
                  </Link>
                ))}
              </nav>
            </SheetContent>
          </Sheet>

          <div className="flex flex-1 items-center justify-end space-x-2">
            <div className="w-full flex-1 md:w-auto md:flex-none">
              {showSearch && (
                <div className="relative hidden md:block">
                  <Input
                    type="search"
                    placeholder="Search..."
                    className="w-64 pr-8"
                    autoFocus
                    onBlur={() => setShowSearch(false)}
                  />
                  <Search className="absolute right-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                </div>
              )}
            </div>
            <nav className="flex items-center">
              <div className="hidden md:block">
                {!showSearch && (
                  <Button variant="ghost" size="icon" onClick={() => setShowSearch(true)}>
                    <Search className="h-5 w-5" />
                    <span className="sr-only">Search</span>
                  </Button>
                )}
              </div>
              <div className="md:hidden">
                <Button variant="ghost" size="icon" onClick={() => setShowSearch(s => !s)}>
                  <Search className="h-5 w-5" />
                  <span className="sr-only">Search</span>
                </Button>
              </div>

              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="icon">
                    {user ? (
                      <Avatar className="h-8 w-8">
                        <AvatarImage src={user.image ?? ''} alt={user.name ?? ''} />
                        <AvatarFallback>{getInitials(user.name)}</AvatarFallback>
                      </Avatar>
                    ) : <User className="h-5 w-5" />}
                    <span className="sr-only">Profile</span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  {isUserLoading ? (
                    <DropdownMenuLabel>Loading...</DropdownMenuLabel>
                  ) : user ? (
                    <>
                      <DropdownMenuLabel>
                        <div className="font-normal">Signed in as</div>
                        <div className="font-semibold">{user.email || (user as any).phoneNumber}</div>
                      </DropdownMenuLabel>
                      <DropdownMenuSeparator />
                      <DropdownMenuItem>
                        <User className="mr-2 h-4 w-4" />
                        <span>Profile</span>
                      </DropdownMenuItem>
                      <DropdownMenuItem>
                        <Package className="mr-2 h-4 w-4" />
                        <span>My Orders</span>
                      </DropdownMenuItem>
                      <DropdownMenuItem>
                        <MapPin className="mr-2 h-4 w-4" />
                        <span>Address</span>
                      </DropdownMenuItem>
                      <DropdownMenuItem>
                        <Settings className="mr-2 h-4 w-4" />
                        <span>Settings</span>
                      </DropdownMenuItem>
                      <DropdownMenuSeparator />
                      <DropdownMenuItem onClick={() => signOut()}>
                        <LogOut className="mr-2 h-4 w-4" />
                        <span>Logout</span>
                      </DropdownMenuItem>
                    </>
                  ) : (
                    <>
                      <DropdownMenuItem onClick={() => setLoginOpen(true)}>
                        Login / Sign Up
                      </DropdownMenuItem>
                    </>
                  )}
                </DropdownMenuContent>
              </DropdownMenu>

              <Sheet>
                <SheetTrigger asChild>
                  <Button variant="ghost" size="icon" className="relative">
                    <ShoppingBag className="h-5 w-5" />
                    <span className="sr-only">Shopping Cart</span>
                    {itemCount > 0 && (
                      <span className="absolute top-0 right-0 flex h-5 w-5 items-center justify-center rounded-full bg-primary text-xs font-bold text-primary-foreground">
                        {itemCount}
                      </span>
                    )}
                  </Button>
                </SheetTrigger>
                <SheetContent>
                  <CartSheetContent />
                </SheetContent>
              </Sheet>
            </nav>
          </div>
        </div>
        {showSearch && (
          <div className="md:hidden border-t animate-in fade-in-0">
            <div className="container p-2">
              <div className="relative">
                <Input
                  type="search"
                  placeholder="Search products..."
                  className="w-full pr-8"
                  autoFocus
                />
                <Search className="absolute right-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
              </div>
            </div>
          </div>
        )}
      </header>
      <LoginDialog open={loginOpen} onOpenChange={setLoginOpen} />
    </>
  );
}
